<template>
  <div class="juben">
    <div class="header">
      <div class="back">
        <img src="../assets/left2.png" alt="" class="back" />
      </div>
    </div>
    <div class="list">
      <div class="top">
        <h3>剧本管理</h3>
        <div class="search">
          <span class="iconfont icon-31sousuo"></span>
          <input type="text" placeholder="请输入剧本名称" />
        </div>
      </div>
      <div class="fenlei">
        <div class="line1 line">
          <span>人数</span>
          <div
            :class="indexa == index1 ? 'active' : 'lists'"
            v-for="(item1, index1) in list1"
            :key="index1"
            :data-aa="item1.number"
            @click="choose1(index1, $event)"
          >
            {{ item1.mc }}
          </div>
        </div>
        <div class="line2 line">
          <span>难度</span>
          <div
            :class="indexb == index2 ? 'active' : 'lists'"
            v-for="(item2, index2) in list2"
            :key="index2"
            :data-aa="item2.id"
            @click="choose2(index2, $event)"
          >
            {{ item2.dict_label }}
          </div>
        </div>
        <div class="line3 line">
          <span>题材</span>
          <div
            :class="indexc == index3 ? 'active' : 'lists'"
            v-for="(item3, index3) in list3"
            :key="index3"
            :data-aa="item3.id"
            @click="choose3(index3, $event)"
          >
            {{ item3.name }}
          </div>
        </div>
        <div class="line4 line">
          <span>类型</span>
          <div
            :class="indexd == index4 ? 'active' : 'lists'"
            v-for="(item4, index4) in list4"
            :key="index4"
            @click="choose4(index4)"
          >
            {{ item4.name }}
          </div>
        </div>
      </div>
    </div>
    <div class="jubenlist">
      <div class="nav" v-for="(jblist, indexjb) in jblist" :key="indexjb">
        {{ jblist.nav }}
      </div>

    </div>
    <div class="content">
      暂无数据
    </div>
  </div>
</template>

<script>
import { num } from "../api/juben";
import { difficulty } from "../api/juben";
import { subjectMatter } from "../api/juben";
import { chooses } from "../api/juben";
export default {
  data() {
    return {
      list1: [],
      list2: [],
      list3: [],
      list4: [{ name: "全部" }, { name: "沉浸式" }, { name: "娱乐式" }],
      indexa: 0,
      indexb: 0,
      indexc: 0,
      indexd: 0,
      jblist: [
        { nav: "编号" },
        { nav: "剧本名称" },
        { nav: "剧本图片" },
        { nav: "价格" },
        { nav: "人数" },
        { nav: "难度" },
        { nav: "类型" },
      ],
    };
  },
  methods: {
    choose1(e, ev) {
      this.indexa = e;
      let num = ev.target.attributes[1].value;
      chooses({ number: num }).then((res) => {
        console.log(res);
      });
    },
    choose2(e, ev) {
      this.indexb = e;
      let level = ev.target.attributes[1].value;
      chooses({ level: level }).then((res) => {
        console.log(res);
      });
    },
    choose3(e, ev) {
      this.indexc = e;
      let styleId = ev.target.attributes[1].value;
      chooses({ styleId: styleId }).then((res) => {
        console.log(res);
      });
    },
    choose4(e) {
      this.indexd = e;
    },
  },
  mounted() {
    // line1
    num().then((res) => {
      let arr = res.data;
      arr.unshift({ mc: "全部" });

      this.list1 = arr;
    });
    // line2
    difficulty().then((res) => {
      let arr = res.data;
      arr.unshift({ dict_label: "全部" });
      console.log(arr);
      this.list2 = res.data;
    });
    // line3
    subjectMatter().then((res) => {
      let arr = res.data;
      arr.unshift({ name: "全部" });
      console.log(arr);
      this.list3 = res.data;
    });
  },
};
</script>

