import request from '../utils/request'
export function num(params) {
    return request ({
        url: '/api/ktv/drama/getNumberList',
        method: 'GET',
        params
    })
}
export function difficulty(params) {
    return request ({
        url: '/api/ktv/classify/updateHcz?type=0&fl=1',
        method: 'GET',
        params
    })
}
export function subjectMatter(params) {
    return request ({
        url: '/api/ktv/classify/updateHcz?type=0&fl=0',
        method: 'GET',
        params
    })
}
export function chooses(params) {
    return request ({
        url: 'api/ktv/drama/list?styleId=&name=&level=&type=&pageNum=1&pageSize=10',
        method: 'GET',
        params
    })
}

